import { HttpClient } from "@angular/common/http";
import { Component } from "@angular/core";
import { Storage } from "@ionic/storage";
import { NavController, App, Events, AlertController } from "ionic-angular";


@Component({
  selector: "page-notes",
  templateUrl: "notes.html"
})
export class NotesPage {
  user: any;
  notes = [];
  HasNotes: Boolean = false;

  constructor(
    public navCtrl: NavController,
    public storage: Storage,
    public http: HttpClient,
    public app: App,
    public events: Events,
    public alertCtrl: AlertController
  ) {
    this.storage.get("user").then(user => {
      if (user !== null) {
        this.user = user;
        this.storage.get("notes").then(notes => {
          if (notes !== null) {
            this.notes = notes.reverse();
            this.HasNotes = true;
          } else {
            this.http
              .get(
                "https://realm-glory.herokuapp.com/api/notes/" + this.user._id
              )
              .subscribe(data => {
                if (data["data"].length > 0) {
                  this.notes = data["data"].reverse();
                  this.storage.set("notes", this.notes);
                }
                this.HasNotes = true;
              });
          }
        });
      }
    });

    this.events.subscribe("note:created", note => {
      this.notes.unshift(note);
      this.storage.set("notes", this.notes);
    });

    this.events.subscribe("note:deleted", noteIndex => {
      this.notes.splice(noteIndex, 1);
      console.log("note removed event");
    });

    this.events.subscribe("note:edited", note => {
      let index = this.notes.indexOf(note);
      if (index > -1) {
        this.notes[index] = note;
      }
    });
  }

  newNote() {
    this.navCtrl.push("NewNotePage");
  }

  openNote(note) {
    this.navCtrl.push("ViewNotePage", { note });
  }

  removeNote(note) {
    const confirm = this.alertCtrl.create({
      title: "Delete Confirmation",
      message: "You really want to delete this note?",
      buttons: [
        {
          text: "Cancel",
          role: "cancel"
        },
        {
          text: "Yes, please",
          handler: () => {
            this.deleteNote(note);
          }
        }
      ]
    });
    confirm.present();
  }

  deleteNote(note) {
    let index = this.notes.indexOf(note);
    // console.log(index, note);

    if (index > -1) {
      this.notes.splice(index, 1);
      this.http
        .post("https://realm-glory.herokuapp.com/api/note/delete", {
          note: note._id,
          user: this.user._id
        })
        .subscribe(status => {
          console.log("removed", status);
          this.storage.set("notes", status["data"]);
        });
    }
  }

  signin() {
    this.app.getRootNavs()[0].setRoot("LandingPage");
  }
}



